.NavbarItems {
    background-color: #023047;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    font-weight: bolder;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 80vw;
    justify-content: center;
    margin-right: 2rem;
}

.nav-links {
    color: whitesmoke;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    background-color: #023d5a;
    color: #ffb703 !important;
    border-radius: 3px;
    transition: all 0.2 ease-out;
}

.fa-bars {
    color: white;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 630px) {
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 400px;
        position: absolute;
        top: 70px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        font-size: 20px;
    }
    
    .nav-menu.active {
        background: #023047;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 1rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #219ebc;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.7rem;
        cursor: pointer;
    }

    .fa-times {
        color: white;
        font-size: 2rem;
    }
}